import { Component, OnInit } from "@angular/core";
import { ApiCallService } from "../../../shared/api-call.service";
import { GlobalVarService } from "../../../shared/global-var.service";
import Swal from "sweetalert2";
import "sweetalert2/src/sweetalert2.scss";

@Component({
  selector: "app-gruppi",
  templateUrl: "./gruppi.component.html",
  styleUrls: ["./gruppi.component.scss"],
})
export class GruppiComponent implements OnInit {
  pag: number = 1; //1 elenco normale, 2 crea, 3 modifica/elimina
  title: string = "Gruppi";

  //variabili form
  nome: string = "";
  idG: number = -1;

  filemsg: string = "Choose a file";
  gruppi: any[] = [];

  constructor(
    private apicalls: ApiCallService,
    public globvar: GlobalVarService
  ) {}

  ngOnInit() {
    //this.gruppi = ["3IA", "4IB", "5IA"];
    this.updateElenco();
  }

  updateElenco() {
    let riss1 = [];
    let riss = this.apicalls.getGruppi();
    riss.then((data) => {
      for (let val in data) {
        riss1.push(data[val]);
      }
      this.gruppi = riss1;
      //console.log(this.gruppi);
    });
  }

  pagCrea() {
    this.title = "Nuovo Gruppo";
    this.pag = 2;
  }

  crea(nome: string) {
    if (nome === "" || nome === undefined) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Form incompleto!",
      });
    } else {
      let riss = this.apicalls.postGruppo(nome);
      riss
        .then((data) => {
          Swal.fire({
            //conferma
            icon: "success",
            title: "<strong>Gruppo creato!</strong>",
            timer: 1500,
          });
          this.pagElenco();
        })
        .catch(() => {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          });
        });
    }
  }

  pagElenco() {
    if (this.pag === 2) {
      this.filemsg = "Choose a file";
      (<HTMLInputElement>document.getElementById("file")).value = ""; //reset del file
    }
    this.title = "Gruppi";
    this.pag = 1;
    this.updateElenco();
    this.nome = "";
    this.idG = -1;
  }
  pagModifica(id: number, desc: string) {
    this.title = "Modifica gruppo: " + desc;
    this.pag = 3;
    this.idG = id;
    this.nome = desc;
  }
  modifica(nome: string) {
    if (nome === "" || nome === undefined) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Form incompleto!",
      });
    } else {
      let riss = this.apicalls.putGruppo(nome, this.idG);
      riss
        .then((data) => {
          Swal.fire({
            //conferma
            icon: "success",
            title: "<strong>Gruppo modificato!</strong>",
            timer: 1500,
          });
          this.pagElenco();
        })
        .catch(() => {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          });
        });
    }
  }
  no() {
    Swal.fire({
      icon: "warning",
      title: "Non disponibile",
      text: "Ci dispiace ma questa funzione non è ancora disponibile.",
    });
  }
  elimina() {
    let riss = this.apicalls.deleteGruppo(this.idG);
    riss
      .then((data) => {
        Swal.fire({
          //conferma
          icon: "success",
          title: "<strong>Gruppo eliminato!</strong>",
          timer: 1500,
        });
        this.pagElenco();
      })
      .catch(() => {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Si è verificato un errore!",
        });
      });
  }
  change1(files: FileList) {
    var nome = files.item(0).name;
    if (!(nome.split(".").pop() === "csv")) {
      Swal.fire({
        icon: "error",
        title: "Non va bene",
        text: "Questo file non è .csv",
      });
      this.filemsg = "Choose a file";
    } else {
      this.filemsg = nome;
    }
  }
}
