import { Injectable } from "@angular/core";
import { Subject } from "rxjs";

/*
@Injectable({
  providedIn: 'root'
})*/
@Injectable()
export class GlobalVarService {
  userRole: number;
  userID: string = ""; //userID o Token
  userName: string = "";

  coll: boolean;

  usrRoleChanger: Subject<number> = new Subject<number>();

  constructor() {
    this.usrRoleChanger.subscribe(value => {
      this.userRole = value;
    });
  }
  changeRole(role: number) {
    this.usrRoleChanger.next((this.userRole = role));
  }
}
