import { Component, OnInit, SimpleChange, OnChanges } from "@angular/core";
import { GlobalVarService } from "../shared/global-var.service";
import { ApiCallService } from "../shared/api-call.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-root",
  templateUrl: "./app-navbar.html",
  styleUrls: ["./app-navbar.scss"],
})
export class AppComponent implements OnInit, OnChanges {
  title = "NewFrontend";
  role: number; //1 guest, 2 student, 3 teacher, 4 admin
  colore: string;
  colGrad: string[] = [
    "linear-gradient(55deg, #030303, #303030)", //1  #6f0000
    "linear-gradient(55deg, #030303, #004e92)", //2  #000428
    "linear-gradient(55deg, #001510 15%, #00af8f)", //3 #181818
    "linear-gradient(55deg, #030303 15%, #007324)", //4
  ];

  constructor(
    private globvar: GlobalVarService,
    private apicalls: ApiCallService,
    private router: Router
  ) {
    this.globvar.usrRoleChanger.subscribe((value) => {
      //console.log("Finalemente: " + value);
      this.role = value;
    });

    let num = Number(sessionStorage.getItem("userRole"));
    if (num != 0) {
      //this.role = Number(sessionStorage.getItem("userRole"));
      this.globvar.userID = sessionStorage.getItem("userID");
      this.globvar.userName = sessionStorage.getItem("userName");
      this.globvar.userRole = Number(sessionStorage.getItem("userRole"));
      this.role = this.globvar.userRole;
    }
    if (num === 0 || num === 1) {
      this.role = 1;
      this.globvar.userRole = this.role;
    }
    /*console.log(
      "roleID: " +
        num +
        "  userID:" +
        sessionStorage.getItem("userID") +
        "  userName:" +
        sessionStorage.getItem("userName")
    );
    console.log("start : " + this.role);*/
  }

  ngOnInit() {
    this.updateSession();
  }

  ngOnChanges(changes: import("@angular/core").SimpleChanges): void {
    throw new Error("Method not implemented.");
  }

  updateSession() {
    let num = Number(sessionStorage.getItem("userRole"));
    if (num === 0 || num === 1) {
      this.role = 1;
      this.globvar.userRole = this.role;
    } else {
      this.role = num;
      this.globvar.userRole = this.role;
      this.globvar.userID = sessionStorage.getItem("userID");
    }
    //console.log("session : " + this.role);
  }

  doLogout() {
    let ress = this.apicalls.getLogout();
    ress.then(() => {
      //console.log("OKK, sei stato disconnesso");
      this.globvar.userID = "";
      this.globvar.userName = "";
      this.globvar.userRole = 1;
      sessionStorage.setItem("userID", this.globvar.userID);
      sessionStorage.setItem("userName", this.globvar.userName);
      sessionStorage.setItem("userRole", String(this.globvar.userRole));
      this.updateSession();
      this.router.navigateByUrl("");
    });
  }

  getPubExercises() {
    //this.apicalls.getPubProblems();
  }

  /*isWRound: boolean = false;
  wRound() {
    if (this.isWRound) return "red";
    else return "";
  }*/
}
