import { Component, OnInit } from "@angular/core";
import { ApiCallService } from "../../../shared/api-call.service";
import Swal from "sweetalert2";
import "sweetalert2/src/sweetalert2.scss";

@Component({
  selector: "app-studenti",
  templateUrl: "./studenti.component.html",
  styleUrls: ["../gruppi/gruppi.component.scss", "./studenti.component.scss"],
})
export class StudentiComponent implements OnInit {
  title: string = "Studenti";
  pag: number = 1; ////1 elenco normale, 2 crea, 3 modifica/elimina
  studenti: any[] = [];
  classi: any[] = [];

  idS: number;
  //variabili form
  nome: string = "";
  cognome: string = "";
  usr: string = "";
  psw: string = "";
  psw1: string = "";

  vis1: boolean = false;
  vis2: boolean = false;

  //variabili check quando si crea
  //inGroup: boolean = false;
  done: boolean = false;

  constructor(private apicalls: ApiCallService) {}

  ngOnInit() {
    this.classi = [
      "Classe  5IA",
      "Classe  5IB",
      "Classe  4IA",
      "Classe  4IB",
      "Classe  3IA",
      "Classe  3IB",
    ];
    this.studenti = ["Tommo"]; //, "Jimmy","Sammy","Manny",];
    this.updateElenco();
  }

  updateElenco() {
    let riss1 = [];
    let riss = this.apicalls.getStudents();
    riss.then((data) => {
      for (let val in data) {
        riss1.push(data[val]);
      }
      this.studenti = riss1;
      //console.log(this.studenti);
    });
  }

  changeVis(sel1: string) {
    if (sel1 === "1") this.vis1 = !this.vis1;
    else this.vis2 = !this.vis2;
  }
  back(num: number) {
    if (num === 2 && this.done) {
      // !this.inGroup &&
      //se ho creato lo studente e non è in gruppo
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Devi inserire lo studente in un gruppo",
      });
    } else {
      //this.inGroup = false;
      this.done = false;
      this.pagElenco();
    }
  }
  pagElenco() {
    this.pag = 1;
    this.title = "Studenti";
    this.updateElenco();
    this.resetForm();
  }
  pagCrea() {
    this.pag = 2;
    this.title = "Nuovo Studente";
    this.getClassi();
  }

  crea() {
    var checkmsg = this.checkForm();
    //this.inGroup = false;
    if (checkmsg != "") {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: checkmsg,
      });
    } else {
      let riss = this.apicalls.postStudent(
        this.nome,
        this.cognome,
        this.usr,
        this.psw
      );
      riss
        .then((data) => {
          Swal.fire({
            //conferma
            icon: "success",
            title: "<strong>Studente aggiunto!</strong>",
            timer: 1500,
          });
          this.done = true;
          this.idS = data["id"];

          //this.pagElenco();
        })
        .catch(() => {
          this.done = false;
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          });
        });
    }
  }

  pagModifica(idS: number, no: string, co: string, usrnm: string) {
    this.pag = 3;
    this.title = "Modifica studente: " + idS;
    this.idS = idS;
    this.nome = no;
    this.cognome = co;
    this.usr = usrnm;
    this.getClassi();
  }
  modifica() {
    var checkmsg = this.checkForm();

    if (checkmsg != "") {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: checkmsg,
      });
    } else {
      let riss = this.apicalls.putStudent(
        this.idS,
        this.nome,
        this.cognome,
        this.usr,
        this.psw
      );
      riss
        .then((data) => {
          Swal.fire({
            //conferma
            icon: "success",
            title: "<strong>Studente modificato!</strong>",
            timer: 1500,
          });
          this.pagElenco();
        })
        .catch(() => {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          });
        });
    }
  }
  putSinG(idG: number) {
    //console.log("classe: " + idG + " | student: " + this.idS);

    let riss = this.apicalls.putSinG(this.idS, idG);
    riss
      .then((data) => {
        if (data == null)
          Swal.fire({
            //conferma
            icon: "success",
            title: "<strong>Studente aggiunto al gruppo!</strong>",
            timer: 1500,
          });
        this.pagElenco();
      })
      .catch(() => {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Si è verificato un errore!",
        });
      });
  }

  delSfromG(idG: number) {
    let riss = this.apicalls.delSfromG(this.idS, idG);
    riss
      .then((data) => {
        Swal.fire({
          //conferma
          icon: "success",
          title: "<strong>Studente rimosso dal gruppo!</strong>",
          timer: 1500,
        });
        //this.pagElenco();
      })
      .catch(() => {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Si è verificato un errore!",
        });
      });
  }

  getClassi() {
    let riss1 = [];
    let riss = this.apicalls.getGruppi();
    riss.then((data) => {
      for (let val in data) {
        data[val]["alreadyIN"] = "0";
        data[val]["hovEff"] = "0";
        riss1.push(data[val]);
      }
      this.classi = riss1;
      //console.log(this.classi);
    });
  }
  elimina() {
    let riss = this.apicalls.deleteStudent(this.idS);
    riss
      .then((data) => {
        Swal.fire({
          //conferma
          icon: "success",
          title: "<strong>Studente eliminato!</strong>",
          timer: 1500,
        });
        this.pagElenco();
      })
      .catch(() => {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Si è verificato un errore!",
        });
      });
  }
  resetForm() {
    this.nome = "";
    this.cognome = "";
    this.usr = "";
    this.psw = "";
    this.psw1 = "";
  }
  checkForm() {
    if (
      this.nome === "" ||
      this.cognome === "" ||
      this.usr === "" ||
      this.psw === "" ||
      this.psw1 === ""
    )
      return "Form incompleto!";
    else if (this.psw != this.psw1) {
      return "Reinserisci correttamente la password";
    } else return "";
  }
}
