import { Component, OnInit } from "@angular/core";
import { ApiCallService } from "../../../shared/api-call.service";
import Swal from "sweetalert2";
import "sweetalert2/src/sweetalert2.scss";

@Component({
  selector: "app-docenti",
  templateUrl: "./docenti.component.html",
  styleUrls: ["../gruppi/gruppi.component.scss", "./docenti.component.scss"],
})
export class DocentiComponent implements OnInit {
  docenti: any[] = [];
  title: string = "Docenti";
  pag: number = 1;

  //variabili form
  nome: string = "";
  cognome: string = "";
  usr: string = "";
  psw: string = "";
  psw1: string = "";
  idT: number;

  vis1: boolean = false;
  vis2: boolean = false;

  constructor(private apicalls: ApiCallService) {}

  ngOnInit() {
    this.docenti = ["Paolo Fuorvianti"];
    this.updateElenco();
  }

  changeVis(sel1: string) {
    if (sel1 === "1") this.vis1 = !this.vis1;
    else this.vis2 = !this.vis2;
  }

  pagElenco() {
    this.pag = 1;
    this.title = "Docenti";
    this.resetForm();
    this.updateElenco();
  }

  updateElenco() {
    let riss1 = [];
    let riss = this.apicalls.getTeachers();
    riss.then((data) => {
      for (let val in data) {
        riss1.push(data[val]);
      }
      this.docenti = riss1;
    });
  }

  pagCrea() {
    this.pag = 2;
    this.title = "Nuovo Docente";
  }

  crea() {
    var checkmsg = this.checkForm();

    if (checkmsg != "") {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: checkmsg,
      });
    } else {
      let riss = this.apicalls.postTeacher(
        this.nome,
        this.cognome,
        this.usr,
        this.psw
      );
      riss
        .then((data) => {
          Swal.fire({
            //conferma
            icon: "success",
            title: "<strong>Docente aggiunto!</strong>",
            timer: 1500,
          });
          this.pagElenco();
        })
        .catch(() => {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          });
        });
    }
  }

  pagModifica(idT: number, no: string, co: string, usrnm: string) {
    this.pag = 3;
    this.title = "Modifica docente: " + idT;
    this.idT = idT;
    this.nome = no;
    this.cognome = co;
    this.usr = usrnm;
  }

  modifica() {
    var checkmsg = this.checkForm();

    if (checkmsg != "") {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: checkmsg,
      });
    } else {
      let riss = this.apicalls.putTeacher(
        this.idT,
        this.nome,
        this.cognome,
        this.usr,
        this.psw
      );
      riss
        .then((data) => {
          if (data != null)
            Swal.fire({
              //conferma
              icon: "success",
              title: "<strong>Docente modificato!</strong>",
            });
          else this.pagElenco();
        })
        .catch(() => {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          });
        });
    }
  }
  elimina() {
    let riss = this.apicalls.deleteTeacher(this.idT);
    riss.then((data) => {
      Swal.fire({
        //conferma
        icon: "success",
        title: "<strong>Docente eliminato!</strong>",
      });
      this.pagElenco();
    });
  }

  resetForm() {
    this.nome = "";
    this.cognome = "";
    this.usr = "";
    this.psw = "";
    this.psw1 = "";
  }
  checkForm() {
    if (
      this.nome === "" ||
      this.cognome === "" ||
      this.usr === "" ||
      this.psw === "" ||
      this.psw1 === ""
    )
      return "Form incompleto!";
    else if (this.psw != this.psw1) {
      return "Reinserisci correttamente la password";
    } else return "";
  }
}
