import { Component, OnInit } from "@angular/core";
import { ApiCallService } from "../../../shared/api-call.service";
import { GlobalVarService } from "../../../shared/global-var.service";
import { FormBuilder, FormGroup } from "@angular/forms";
import Swal from "sweetalert2";
import "sweetalert2/src/sweetalert2.scss";
import { DomSanitizer, SafeResourceUrl } from "@angular/platform-browser";

@Component({
  selector: "app-exercises",
  templateUrl: "./exercises.component.html",
  styleUrls: [
    "../gruppi/gruppi.component.scss",
    "./exercises.component.scss",
    "../verifiche/verifiche.component.scss",
  ],
})
export class ExercisesComponent implements OnInit {
  pag: number = 1; //1 elenco, 2 crea | pdf problema, 3 modifica, 4  submissions
  title: string = "Esercizi";

  formGroup: FormGroup;
  //variabili from
  nome: string = "";
  nomes: string = "";
  probVis: boolean = false;
  liv: number = 0;
  cat: number = 0;
  nTestC: number = 0;
  catList: string[];
  //crea modifica esercizio
  files: File[] = [null, null, null]; //0 description, 1 solution, 2 material
  filesNames: string[] = ["Choose a file", "Choose a file", "Choose a file"]; //0 description, 1 solution, 2 material

  //task
  file1: File = null;
  fileName1: string = "";
  filemsg: string = "Choose a file";

  idE: number;
  filePDF: SafeResourceUrl; // url file pdf

  esercizi: any[] = [];
  constructor(
    private apicalls: ApiCallService,
    private fb: FormBuilder,
    private domSanitizer: DomSanitizer,
    public globvar: GlobalVarService
  ) {}

  //metodi di prova
  ciap(num: number) {
    //console.log("ciao " + num);
  }

  ngOnInit() {
    this.esercizi = [
      "Numeri Perfetti",
    ]; /*,
      "Pacchi Postali 2",
      "Asini e Vacche",
      "Gara di capre",
      "Asini e Vacche",
      "Plants vs Zombies",
      "Plants vs Zombies2",
      "Plants vs Zombies3",
      "Canguri Saltellanti"
    ];*/
    this.formGroup = this.fb.group({
      task: [""],
      description: [""],
      solution: [""],
      material: [""],
    });
    if (this.globvar.userRole === 1 || this.globvar.userRole === 2) {
      // se guest o studente
      //console.log("publicEx");
      this.updateElenco(true);
    } else {
      //console.log("privateEx");
      this.updateElenco(false);
    }
  }

  giveStyle() {
    switch (this.globvar.userRole) {
      case 1:
        return "card cardGuest";
        break;
      case 2:
        return "card cardStud";
        break;
      case 3:
        return "card";
        break;
      case 4:
        return "card";
        break;
    }
  }

  cambiaVis() {
    this.probVis = !this.probVis;
    //console.log("Stato " + this.probVis);
  }
  getID(id: number) {
    console.log(id);
  }
  pagPdf() {
    this.pag = 2;
    this.filemsg = "Choose a file";
    this.formGroup.get("task").setValue(null);
    this.file1 = null;
  }

  updateElenco(pub: boolean) {
    let riss;
    if (pub) {
      riss = this.apicalls.getPubProblems();
      this.filemsg = "Choose a file";
      this.formGroup.get("task").setValue(null);
      this.file1 = null;
    } else {
      riss = this.apicalls.getTasks();
      this.formGroup.get("description").setValue(null);
      this.formGroup.get("solution").setValue(null);
      this.formGroup.get("material").setValue(null);
      this.filesNames = ["Choose a file", "Choose a file", "Choose a file"];
      this.files[0] = null;
      this.files[1] = null;
      this.files[2] = null;
    }
    riss.then((data) => {
      this.esercizi = [];
      for (let val in data) {
        this.esercizi.push(data[val]);
      }
      //console.log(this.esercizi);
    });
  }
  pagElenco(pub: boolean) {
    this.title = "Esercizi";
    this.pag = 1;
    this.updateElenco(pub);

    this.nome = "";
    this.nomes = "";
    this.probVis = false;
    this.liv = 0;
    this.cat = 0;
    this.nTestC = 0;
    this.files = [null, null, null];
  }
  pagModifica(idE: number) {
    this.idE = idE;
    this.title = "Modifica Esercizio";
    this.pag = 3; // modifica / crea
    let riss = this.apicalls.getTaskById(idE);
    riss
      .then((data) => {
        //console.log(data);
        this.nome = data["title"];
        this.nomes = data["short_title"];
        this.probVis = data["is_public"] === "1" ? true : false;
        (<HTMLSelectElement>document.getElementsByName("livello")[0]).value =
          data["level"];

        (<HTMLSelectElement>document.getElementsByName("test_cases")[0]).value =
          data["test_cases"];

        let riss1 = this.apicalls.getCategories();
        let rss = [];
        riss1.then((data1) => {
          for (let dd in data1) {
            if (data1[dd]["id"] === data["category_id"]) {
              rss.push(data1[dd]);
              break;
            }
          }
          for (let dd in data1) {
            if (data1[dd]["id"] != data["category_id"]) {
              rss.push(data1[dd]);
            }
          }
          this.catList = rss;
        });
      })
      .catch(this.apicalls.handleError);
  }
  elimina() {
    let riss = this.apicalls.deleteTasks(this.idE);

    riss.then((data) => {
      Swal.fire({
        //conferma
        icon: "success",
        title: "<strong>Esercizio eliminato!</strong>",
        timer: 1000,
      });
      this.pagElenco(false);
    });
  }
  pagProblema(idE: number, nome: string) {
    this.pag = 2;
    this.nome = nome;
    this.idE = idE;
    this.title = "Esercizio : " + this.nome;
    //console.log(idE + " " + this.nome);
    let riss = this.apicalls.getProblemPdf(this.idE);
    riss.subscribe(
      (downloaded) => {
        //console.log("ciao1" + "  " + downloaded);
        this.filePDF = this.domSanitizer.bypassSecurityTrustResourceUrl(
          window.URL.createObjectURL(downloaded)
        );
      },
      (error) => {
        console.log(error);
        //console.log("ciao2");
      }
    );
  }
  pagSottomissione() {
    this.pag = 4;
    //this.title = "Esercizio : " + this.nome;
    //console.log("Sottomissione " + this.idE + " " + this.nome);
  }
  pagCrea() {
    this.title = "Crea Esercizio";
    this.pag = 2;
    let riss = this.apicalls.getCategories();
    let rss = [];
    riss.then((data) => {
      for (let dd in data) {
        rss.push(data[dd]);
      }
      this.catList = rss;
      console.log(this.catList);
    });
    //console.log("Pagina crea " + this.pag);
  }

  change1(sel: number, files: FileList) {
    //console.log(sel);
    if (files.length > 0) {
      var nome = files.item(0).name;
      //console.log(nome);
      switch (sel) {
        case 0:
          if (!(nome.split(".").pop() === "pdf")) {
            // non pdf
            Swal.fire({
              icon: "error",
              title: "File non supportato",
              text: "Questo file non ha l'estensione .pdf",
            });
            this.filesNames[0] = "Choose a file";
          } else {
            this.filesNames[0] = nome;
            this.files[sel] = files.item(0);
            this.formGroup.get("description").setValue(this.files[sel]);
            /*console.log(
              "File " +
                nome +
                "caricato " +
                this.files[sel] +
                " size: " +
                this.files[sel].size
            );*/
          }
          break;
        case 1:
          if (nome.split(".").pop() === "rar") {
            // non zip
            Swal.fire({
              icon: "error",
              title: "File non supportato",
              text: "Questo file ha estensione .rar",
            });
            this.filesNames[1] = "Choose a file";
          } else if (!(nome.split(".").pop() === "zip")) {
            Swal.fire({
              icon: "error",
              title: "File non supportato",
              text: "Questo file non ha l'estensione .zip",
            });
            this.filesNames[1] = "Choose a file";
          } else {
            this.filesNames[1] = nome;
            this.files[sel] = files.item(0);
            this.formGroup.get("solution").setValue(this.files[sel]);
            /*console.log(
              "File " +
                nome +
                "caricato " +
                this.files[sel] +
                " size: " +
                this.files[sel].size
            );*/
          }
          break;
        case 2:
          if (nome.split(".").pop() === "rar") {
            // non zip
            Swal.fire({
              icon: "error",
              title: "File non supportato",
              text: "Questo file ha estensione .rar",
            });
            this.filesNames[2] = "Choose a file";
          } else if (!(nome.split(".").pop() === "zip")) {
            Swal.fire({
              icon: "error",
              title: "File non supportato",
              text: "Questo file non ha l'estensione .zip",
            });
            this.filesNames[2] = "Choose a file";
          } else {
            this.filesNames[2] = nome;
            this.files[sel] = files.item(0);
            this.formGroup.get("material").setValue(this.files[sel]);
            /*console.log(
              "File " +
                nome +
                "caricato " +
                this.files[sel] +
                " size: " +
                this.files[sel].size
            );*/
          }
          break;
        default: {
          Swal.fire({
            icon: "error",
            title: "Non va bene",
            text: "Errore",
          });
        }
      }
    }
  }
  changeTask(files: FileList) {
    if (files.length > 0) {
      var nome = files.item(0).name;
      if (!(nome.split(".").pop() === "c")) {
        //non .c
        Swal.fire({
          icon: "error",
          title: "File non supportato",
          text: "Questo file non ha l'estensione .c",
        });
        this.filemsg = "Choose a file";
      } else {
        this.filemsg = nome;
        this.file1 = files.item(0);
        this.formGroup.get("task").setValue(this.file1);
        /*console.log(
          "File caricato " + this.file1.name + " size: " + this.file1.size
        );*/
      }
    }
  }
  caricaSub(idE: number) {
    if (this.file1 != null) {
      //puoi mandarlo
      const form: FormData = new FormData(); //creazione oggetto form da mandare
      form.append("submission", this.file1);
      let riss1 = this.apicalls.postSubmission(idE, form);
      riss1
        .then((data) => {
          let lung = data.lines.length;
          let ristr = ((Number.parseFloat(data.score) / lung) * 100)
            .toString()
            .substring(0, 5);
          let punt = Number.parseFloat(ristr);
          console.log(punt);
          //console.log(punt);
          if (punt >= 60.0)
            Swal.fire({
              //conferma
              icon: "success",
              title: "<strong>Risultato</strong>",
              text: "Punteggio: " + punt + "/100",
            });
          else
            Swal.fire({
              //conferma
              icon: "warning",
              title: "<strong>Risultato</strong>",
              html:
                "<strong>Punteggio: " +
                punt +
                "/100" +
                "</strong><br><br> Esercitati ancora!",
            });
          //this.pagElenco(false);
          //console.log(data);
        })
        .catch(() =>
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          })
        );
    } else {
      //non si può mandare perchè manca il file
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Non hai indicato il file da caricare!",
      });
    }
  }

  crea_modifica(num: number) {
    this.liv = Number.parseInt(
      (document.getElementsByName("livello")[0] as HTMLInputElement).value
    );

    this.cat = Number.parseInt(
      (document.getElementsByName("category_id")[0] as HTMLInputElement).value
    );
    this.nTestC = Number.parseInt(
      (document.getElementsByName("test_cases")[0] as HTMLInputElement).value
    );
    const form: FormData = new FormData(); //creazione oggetto form da mandare

    form.append("title", this.nome);
    form.append("short_title", this.nomes);
    form.append("is_public", this.probVis ? "1" : "0");
    form.append("level", this.liv + "");
    form.append("test_cases", this.nTestC + "");
    form.append("category_id", this.cat + "");
    let varrr1 = this.formGroup.get("description").value;
    if (this.files[0] != null) form.append("description", varrr1);
    let varrr2 = this.formGroup.get("solution").value;
    if (this.files[1] != null) form.append("solution", varrr2);
    let varrr3 = this.formGroup.get("material").value;
    if (this.files[2] != null) form.append("material", varrr3);
    varrr1 = "";
    varrr2 = "";
    varrr3 = "";

    //form.forEach((val) => console.log(val));
    if (this.checkForm()) {
      if (num === 1) {
        //crea
        if (
          this.files[0] != null &&
          this.files[1] != null &&
          this.files[2] != null
        ) {
          let riss1 = this.apicalls.postTasks(form);
          riss1
            .then((data) => {
              Swal.fire({
                //conferma
                icon: "success",
                title: "<strong>Esercizio aggiunto!</strong>",
                timer: 1000,
              });
              this.pagElenco(false);
              //console.log(data);
            })
            .catch(() =>
              Swal.fire({
                icon: "error",
                title: "Oops...",
                text: "Si è verificato un errore!",
              })
            );
        } else {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Non hai caricato alcuni file!",
          });
        }
      } else {
        //modifica
        let riss1 = this.apicalls.postTask(this.idE, form);
        riss1
          .then((data) => {
            Swal.fire({
              //conferma
              icon: "success",
              title: "<strong>Esercizio modificato!</strong>",
              timer: 1000,
            });
            this.pagElenco(false);
            //console.log(data);
          })
          .catch(() =>
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "Si è verificato un errore!",
            })
          );
      }
    } else {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Form incompleto!",
      });
    }
  }

  checkForm() {
    if (
      this.nome === "" ||
      this.nomes === "" ||
      this.liv === 0 ||
      //this.cat === 0 ||
      this.nTestC === 0
    )
      return false;
    else return true;
  }
}
