import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { HomepageComponent } from "./staticPages/homepage/homepage.component";
import { AboutComponent } from "./staticPages/about/about.component";
import { ExercisesComponent } from "./dynamicPages/exercises/exercises.component";
import { GruppiComponent } from "./dynamicPages/gruppi/gruppi.component";
import { DocentiComponent } from "./dynamicPages/docenti/docenti.component";
import { StudentiComponent } from "./dynamicPages/studenti/studenti.component";
import { VerificheComponent } from "./dynamicPages/verifiche/verifiche.component";
import { LoginComponent } from "./staticPages/login/login.component";
import { NienteComponent } from "./dynamicPages/niente/niente.component";
import { G1Guard } from "../shared/guards/g1.guard";
import { G2Guard } from "../shared/guards/g2.guard";
import { G3Guard } from "../shared/guards/g3.guard";
import { G4Guard } from "../shared/guards/g4.guard";
import { G5Guard } from "../shared/guards/g5.guard";

const routes: Routes = [
  { path: "", component: HomepageComponent, pathMatch: "full" }, //homepage  #
  //da sistemare
  { path: "docenti", component: DocentiComponent, canActivate: [G1Guard] }, //docenti   #
  { path: "studenti", component: StudentiComponent, canActivate: [G2Guard] }, //studenti  @
  { path: "gruppi", component: GruppiComponent, canActivate: [G3Guard] }, //gruppi    @
  {
    path: "verifiche",
    component: VerificheComponent,
    canActivate: [G5Guard],
  }, //verifiche
  { path: "sleep", component: NienteComponent }, //problemi
  { path: "esercizi", component: ExercisesComponent }, //esercizi #
  { path: "notFound", component: NienteComponent }, //disconnetti
  ///
  { path: "login", component: LoginComponent, canActivate: [G4Guard] }, //login #
  { path: "about", component: AboutComponent }, //about  #
  { path: "**", redirectTo: "/notFound" },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
