@Component({
  selector: "app-verifiche",
  templateUrl: "./verifiche.component.html",
  styleUrls: ["../gruppi/gruppi.component.scss", "./verifiche.component.scss"],
})
export class VerificheComponent implements OnInit {
  pag: number = 1; ////1 elenco normale, 2 crea, 3 modifica/elimina, 4 risultati || studenti 4 elenco esercizi, 5 pdf, 6 submission
  title: string = "Verifiche";

  //variabili form
  nome: string = "";
  verAttiva: boolean = false;
  idV: number = -1;
  idE: number = -1;

  verifiche: any[] = [];

  esLista: any[] = [];
  catLista: any[];
  studsRis: any[] = [];
  studRis: any[] = [];

  hovEffect: boolean = false;
  msgg: string = "Verifiche non trovate";
  filemsg: string = "Choose a file";
  filePDF: SafeResourceUrl; // url file pdf

  //submission
  file1: File = null;
  fileName1: string = "";
  formGroup: FormGroup;

  constructor(
    private apicalls: ApiCallService,
    public globvar: GlobalVarService,
    private domSanitizer: DomSanitizer,
    private fb: FormBuilder
  ) {}

  ngOnInit() {
    this.verifiche = [
      "Verifica 12-01",
    ]; /*,
      "Verifica 18-01",
      "Verifica 23-01",
      "Verifica 30-01"
    ];*/
    //let riss = this.apicalls.getVerifiche();
    this.updateElenco();
    this.formGroup = this.fb.group({
      task: [""],
    });
    //console.log(this.verifiche);
  }

  prova(id: number) {
    alert("Funziona " + id);
  }
  risultati() {
    this.pag = 4;
    this.title = "Risultati : " + this.nome;
    this.getRisultati();
  }

  getAllTestDetailsStud(idStud: number, nome: string, cognome: string) {
    let riss1 = this.apicalls.getTestDetailStud(this.idV, idStud);
    riss1
      .then((data) => {
        //scarico i file
        let strr = "";
        var errr = false;
        strr +=
          "<table style='text-align: center; width: 100%;'><tr><th>Esercizio</th><th>Punteggio</th></tr>";
        for (let val in data) {
          strr +=
            "<tr><td>" +
            data[val].short_title +
            "</td><td>" +
            data[val].score +
            " / " +
            data[val].test_cases +
            "</td></tr>";

          var idTask = data[val].task_id;
          //console.log(
          //  "test: " + this.idV + " | task: " + idTask + " | idStud: " + idStud
          //);
          let riss2 = this.apicalls.getTestTaskStud(this.idV, idTask, idStud);
          riss2.subscribe(
            (downloaded) => {
              //console.log("file" + "  " + downloaded);
              let blob: any = new Blob([downloaded], {
                type: "text/x-c;charset=UTF-8",
              });
              let fileC = window.URL.createObjectURL(blob);

              const link = document.createElement("a");
              link.setAttribute("target", "_blank");
              link.setAttribute("href", fileC);
              link.setAttribute(
                "download",
                `${data[val].surname}_${data[val].short_title}.c`
              );
              document.body.appendChild(link);
              link.click();
              link.remove();
            },
            (error) => {
              errr = true;
              //console.log(error);
              Swal.fire({
                icon: "error",
                title: "Oops",
                text: "Si è verificato un errore.",
              });
            }
          );
        }
        if (!errr) {
          strr +=
            "</table><br><h4 style='font-family='Lato', 'san-serif'; color: darkgrey; font-size=15px;'>I file richiesti stanno arrivando</h4>";
          Swal.fire({
            icon: "info",
            title: cognome + " " + nome,
            html: strr,
          });
          //console.log(data);
        }
      })
      .catch(() => {
        Swal.fire({
          icon: "error",
          title: "Oops",
          text: "Si è verificato un errore.",
        });
      });
  }

  getTestDetailsStud(idStud: number, nome: string, cognome: string) {
    let riss1 = this.apicalls.getTestDetailStud(this.idV, idStud);
    riss1
      .then((data) => {
        let strr = "";
        strr +=
          "<table style='text-align: center; width: 100%;'><tr><th>Esercizio</th><th>Punteggio</th><th></th></tr>";
        for (let val in data) {
          strr +=
            "<tr><td>" +
            data[val].short_title +
            "</td><td>" +
            data[val].score +
            " / " +
            data[val].test_cases +
            "</td></tr>";
        }
        strr += "</table>";
        Swal.fire({
          icon: "info",
          title: cognome + " " + nome,
          html: strr,
        });
        //console.log(data);
      })
      .catch(() => {
        Swal.fire({
          icon: "error",
          title: "Oops",
          text: "Si è verificato un errore.",
        });
      });
  }
  getRisultati() {
    let riss = [];
    let riss1 = this.apicalls.getTestResult(this.idV);
    riss1.then((data) => {
      for (let val in data) {
        data[val].result = data[val].result.substring(0, 5);
        riss.push(data[val]);
      }
      this.studsRis = riss;
      console.log(this.studsRis);
    });
  }
  getRisultatiCSV() {
    // da sistemare
    let riss1 = this.apicalls.getTestResultCSV(this.idV);
    riss1.subscribe(
      (downloaded) => {
        console.log("ciao1" + "  " + downloaded);
        //console.log( downloaded.type + "\n" + downloaded.text().then((data) => console.log(data)));

        let blob: any = new Blob([downloaded], {
          type: "data:text/csv;charset=utf-8",
        });
        //fileSaver.saveAs(blob, 'employees.json');
        let fileCSV = window.URL.createObjectURL(blob); //this.domSanitizer.bypassSecurityTrustResourceUrl(
        //);

        const link = document.createElement("a");
        link.setAttribute("target", "_blank");
        link.setAttribute("href", fileCSV);
        link.setAttribute("download", `results.csv`);
        document.body.appendChild(link);
        link.click();
        link.remove();
      },
      (error) => {
        console.log(error);
        //console.log("ciao2");
      }
    );
  }
  backFromRisultati() {
    this.title = "Modifica : " + this.nome;
    this.pag = 3;
  }

  pagEsercizi(idV: number, nome: string) {
    //elenco es. 1 verifica
    this.pag = 4;
    this.msgg = "Esercizi non trovati";
    this.idV = idV;
    this.nome = nome;
    this.title = nome;

    this.filePDF = null;
    let riss1 = this.apicalls.getTasksByTestId(this.idV);
    riss1.then((data) => {
      this.esLista = [];
      for (let val in data) {
        this.esLista.push(data[val]);
      }
      //console.log(this.esLista);
    });
  }
  pagEserciziOK() {
    this.pag = 4;
    this.title = this.nome;
  }

  pagProblema(idE: number, nome: string) {
    this.pag = 5;
    this.title = "Esercizio : " + nome;
    //console.log(idE + " " + nome);
    this.filemsg = "Choose a file";
    this.formGroup.get("task").setValue(null);
    this.file1 = null;
    this.idE = idE;
    let riss = this.apicalls.getProblemPdf1(idE);
    riss.subscribe(
      (downloaded) => {
        //console.log("ciao1" + "  " + downloaded);
        this.filePDF = this.domSanitizer.bypassSecurityTrustResourceUrl(
          window.URL.createObjectURL(downloaded)
        );
      },
      (error) => {
        console.log(error);
        //console.log("ciao2");
      }
    );
  }
  pagPdf() {
    this.pag = 5;
    this.filemsg = "Choose a file";
    this.formGroup.get("task").setValue(null);
    this.file1 = null;
  }
  pagSottomissione() {
    this.pag = 6;
  }

  changeTask(files: FileList) {
    if (files.length > 0) {
      var nome = files.item(0).name;
      if (!(nome.split(".").pop() === "c")) {
        //non .c
        Swal.fire({
          icon: "error",
          title: "Non va bene",
          text: "Questo file non ha l'estensione .c",
        });
        this.filemsg = "Choose a file";
      } else {
        this.filemsg = nome;
        this.file1 = files.item(0);
        this.formGroup.get("task").setValue(this.file1);
        /*console.log(
          "File caricato " + this.file1.name + " size: " + this.file1.size
        );*/
      }
    }
  }
  caricaSub() {
    //console.log(this.idV + " " + this.idE);

    if (this.file1 != null) {
      //puoi mandarlo
      const form: FormData = new FormData(); //creazione oggetto form da mandare
      form.append("submission", this.file1);
      let riss1 = this.apicalls.postSubmissionT(this.idE, this.idV, form);
      riss1
        .then((data) => {
          let lung = data.lines.length;
          let ristr = ((Number.parseFloat(data.score) / lung) * 100)
            .toString()
            .substring(0, 5);
          let punt = Number.parseFloat(ristr);
          //console.log(punt);
          if (punt >= 60.0)
            Swal.fire({
              //conferma
              icon: "success",
              title: "<strong>Risultato</strong>",
              text: "Punteggio: " + punt + "/100",
            });
          else
            Swal.fire({
              //conferma
              icon: "warning",
              title: "<strong>Risultato</strong>",
              html:
                "<strong>Punteggio: " +
                punt +
                "/100" +
                "</strong><br><br> Esercitati ancora!",
            });
          //this.pagElenco(false);
          //console.log(data);
        })
        .catch(() =>
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Si è verificato un errore!",
          })
        );
    } else {
      //non si può mandare perchè manca il file
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "Non hai indicato il file da caricare!",
      });
    }
  }

  updateElenco() {
    let riss = this.apicalls.getVerifiche();
    riss.then((data) => {
      this.verifiche = [];
      for (let val in data) {
        this.verifiche.push(data[val]);
      }
      //console.log(this.verifiche);
    });
  }

  pagElenco() {
    this.title = "Verifiche";
    this.pag = 1;
    this.updateElenco();
    this.msgg = "Verifiche non trovate";
    this.nome = "";
    this.verAttiva = false;
    this.esLista = [];
    this.catLista = [];
  }

  elencoEsTab() {
    let riss = this.apicalls.getTasks();
    let rss = [];
    let riss1 = this.apicalls.getTasksByTestId(this.idV);
    let esAlreadyIN = []; // es già dentro in verifica

    let rissCat = this.apicalls.getCategories();
    rissCat
      .then((data) => {
        for (let dd in data) {
          rss.push(data[dd]); //preparo le categorie
        }
        this.catLista = rss;
        //console.log(this.catLista);
      })
      .then(() => {
        riss.then((data) => {
          this.esLista = [];
          rss = [];
          for (let val in data) {
            let rss1 = this.apicalls.getTaskById(data[val].id);
            rss1.then((data1) => {
              if (data1["is_public"] === "0") {
                riss1.then((data11) => {
                  if (data11["length"] === 0) data1["alreadyIN"] = "0";
                  else {
                    for (let val1 in data11) {
                      if (data11[val1]["title"] === data1["title"]) {
                        data1["alreadyIN"] = "1";
                        break;
                      } else {
                        data1["alreadyIN"] = "0";
                      }
                    }
                  }
                });
                data1["hovEff"] = "0";
                rss.push(data1);
              }
            });
            //rss.push(data[val]);
          }
          this.esLista = rss;
          //console.log(this.esLista); //lista degli es. da stampare

          riss1.then((data11) => {
            //esercizi già dentro
            //console.log(data11);
          });
        });
      });
  }

  pagModifica(id: number, desc: string, vis: string) {
    this.title = "Modifica : " + desc;
    this.pag = 3;
    this.idV = id;
    let vall = vis === "1" ? true : false;
    this.verAttiva = vall;
    this.nome = desc;
    //console.log(id + " " + desc + " " + vis);
    this.elencoEsTab();
  }
  aggiungiEs(idE: number) {
    let riss = this.apicalls.putTaskInTest(this.idV, idE);
    riss.then(() => {
      //console.log("Esercizio Aggiunto!");
      for (let val32 in this.esLista) {
        if (this.esLista[val32]["id"] === idE)
          this.esLista[val32]["alreadyIN"] = "1";
      }
      Swal.fire({
        //conferma
        icon: "success",
        title: "<strong>Esercizio aggiunto!</strong>",
        timer: 1000,
      });
      //this.elencoEsTab();
    });
  }

  togliEs(idE: number) {
    let riss2 = this.apicalls.remTaskFromTest(this.idV, idE);
    riss2.then((ris) => {
      //console.log("Esercizio Rimosso!");
      for (let val32 in this.esLista) {
        if (this.esLista[val32]["id"] === idE)
          this.esLista[val32]["alreadyIN"] = "0";
      }
      Swal.fire({
        //conferma
        icon: "success",
        title: "<strong>Esercizio rimosso!</strong>",
        timer: 1000,
      });
      //this.elencoEsTab();
    });
  }

  modifica() {
    if (this.nome === "" || this.nome === undefined) {
      alert("Form incompleto!");
    } else {
      let riss = this.apicalls.putTest(this.nome, this.idV, this.verAttiva);
      riss.then((data) => {
        this.pagElenco();
      });
    }
  }
  pagCrea() {
    this.title = "Crea Verifica";
    this.pag = 2;
  }
  crea(nome: string) {
    if (nome === "" || nome === undefined) {
      alert("Form incompleto!");
    } else {
      let riss = this.apicalls.postTest(nome);
      riss.then((data) => {
        this.pagElenco();
      });
    }
  }
  cambiaVis() {
    this.verAttiva = !this.verAttiva;
  }
  elimina() {
    let riss = this.apicalls.deleteTest(this.idV);
    riss.then((data) => {
      this.pagElenco();
    });
  }
}
