import { Injectable } from "@angular/core";
import { CanActivate, Router } from "@angular/router";
import { GlobalVarService } from "src/shared/global-var.service";

@Injectable({
  providedIn: "root",
})
export class G1Guard implements CanActivate {
  constructor(private _router: Router, private _globvar: GlobalVarService) {}

  canActivate(): boolean {
    //guard docenti
    if (this._globvar.userRole === 4) return true;
    else {
      this._router.navigateByUrl("");
      return false;
    }
  }
}
